header {
    margin: 0;
    padding: 0;
    menu{
        margin: 0;
        padding: 0;
        .menu_item{
            width: 16.66vw;
            height: 16.67vw;
            display: inline-block;
            background: linear-gradient(red, deeppink)
        }
    }    
}

